import React,{useState} from "react";
import { ImgHolder, ItemDetails } from "./general/style/style";
import { getDefaultImage, DhProperties } from "../DhProperties";
import { TvCardContainer } from "./gadget/style/gadgetStyle";
//import { toJS } from "mobx";


export default function TvModelItem({data, isFallbackCase,lang="en"}) {

  const [imageLoadError,  setImageLoadError] = useState(false);

    const onItemClick = (id,product, title='') => {
    try{
      if(typeof(newsHuntAction)!=='undefined' && typeof(callEvent)!=="undefined" && ((!isIos() && checkMinAndroidAppVersion(NH_BROWSER_SUPPORTED_VERSION)) || (isIos() && checkMinAndroidAppVersion(NH_BROWSER_SUPPORTED_VERSION_IOS))) && title && id && product){
          const url= DhProperties.properties.pwaBaseUrl+"/webItem?webModel="+encodeURIComponent(JSON.stringify({title:title,url:DhProperties.properties.baseUrl+`/details?product=${product}&id=${id}&lang=${lang}`}));
          callEvent(url);
      } else if(product && id){ 
       window.location.href = DhProperties.properties.baseUrl+`/details?product=${product}&id=${id}&lang=${lang}`;
      }
    }catch(err){
      console.log("TvModelItem: ",err);
    }
  }


  return (
    <TvCardContainer onClick={()=>onItemClick(data.id, data.product, data.name)} key={data.id} isFallbackCase={isFallbackCase}>
      <ImgHolder> 
        {data.image && <img src={imageLoadError?getDefaultImage(data.product):data.image} alt={data.model} onError={()=>setImageLoadError(true)}/>}
        </ImgHolder>
        <ItemDetails className='laptop'>
         {data.name && <p>{data.name}...</p> }
         {data.info && <span class="features" title={data.info}>{data.info}</span> }
         {data.price && <span class="price">â‚¹{data.price}</span>}
      </ItemDetails>
    </TvCardContainer>
  );
}

